// src/components/Explanation.js
import React from 'react';
import { Container, Typography, Box, Paper, List, ListItem, ListItemText, ListItemIcon } from '@mui/material';
import LockIcon from '@mui/icons-material/Lock';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import CancelIcon from '@mui/icons-material/Cancel';
import InfoIcon from '@mui/icons-material/Info';
import RotateRightIcon from '@mui/icons-material/RotateRight';
import styled from '@emotion/styled';

const Section = styled(Box)`
  margin: 20px 0;
  padding: 20px;
  background-color: #f0f2f5;
  border-radius: 8px;
`;

const Explanation = () => {
  return (
    <Container>
      <Typography variant="h3" component="h1" gutterBottom sx={{ textAlign: 'center', mt: 5 }}>
        Cryptography Algorithms Explained
      </Typography>
      
      <Section>
        <Typography variant="h4" gutterBottom>
          <LockIcon fontSize="large" /> Caesar Cipher
        </Typography>
        <Typography variant="body1" paragraph>
          The Caesar Cipher is one of the simplest and oldest methods of encrypting messages, named after Julius Caesar. This technique involves shifting the letters of the alphabet by a fixed number of places. For example, with a shift of three, the letter ‘A’ becomes ‘D’, ‘B’ becomes ‘E’, and so on.
        </Typography>
        
        <Typography variant="h5" gutterBottom>
          What is Caesar Cipher Technique?
        </Typography>
        <Typography variant="body1" paragraph>
          The Caesar cipher is a simple encryption technique used by Julius Caesar to send secret messages to his allies. It works by shifting the letters in the plaintext message by a certain number of positions, known as the “shift” or “key”.
        </Typography>
        
        <Typography variant="h5" gutterBottom>
          Cryptography Algorithm For the Caesar Cipher
        </Typography>
        <Typography variant="body1" paragraph>
          To cipher a given text, you need an integer value known as the shift, which indicates the number of positions each letter of the text has been moved down. The encryption can be represented using modular arithmetic by transforming the letters into numbers. 
        </Typography>
        
        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <Typography variant="body1" paragraph>
            For example, if the shift is 3, then the letter A would be replaced by the letter D, B would become E, C would become F, and so on. The alphabet is wrapped around so that after Z, it starts back at A.
          </Typography>
          <List>
            <ListItem>
              <ListItemIcon><InfoIcon /></ListItemIcon>
              <ListItemText primary="Write down the plaintext message: HELLO" />
            </ListItem>
            <ListItem>
              <ListItemIcon><InfoIcon /></ListItemIcon>
              <ListItemText primary="Choose a shift value. In this case, we will use a shift of 3." />
            </ListItem>
            <ListItem>
              <ListItemIcon><InfoIcon /></ListItemIcon>
              <ListItemText primary="Replace each letter in the plaintext message with the letter that is three positions to the right in the alphabet." />
            </ListItem>
          </List>
          <Typography variant="body1" paragraph>
            - H becomes K (shift 3 from H)  
            - E becomes H (shift 3 from E)  
            - L becomes O (shift 3 from L)  
            - O becomes R (shift 3 from O)
          </Typography>
          <Typography variant="h6">
            The encrypted message is now “KHOOR”.
          </Typography>
        </Paper>
        
        <Typography variant="h5" gutterBottom>
          Advantages
        </Typography>
        <List>
          <ListItem>
            <ListItemIcon><CheckCircleIcon color="primary" /></ListItemIcon>
            <ListItemText primary="Easy to implement and use, making it suitable for beginners." />
          </ListItem>
          <ListItem>
            <ListItemIcon><CheckCircleIcon color="primary" /></ListItemIcon>
            <ListItemText primary="Can be physically implemented with rotating disks or cards." />
          </ListItem>
          <ListItem>
            <ListItemIcon><CheckCircleIcon color="primary" /></ListItemIcon>
            <ListItemText primary="Requires only a small set of pre-shared information." />
          </ListItem>
          <ListItem>
            <ListItemIcon><CheckCircleIcon color="primary" /></ListItemIcon>
            <ListItemText primary="Can be modified easily to create a more secure variant." />
          </ListItem>
        </List>
        
        <Typography variant="h5" gutterBottom>
          Disadvantages
        </Typography>
        <List>
          <ListItem>
            <ListItemIcon><CancelIcon color="error" /></ListItemIcon>
            <ListItemText primary="Not secure against modern decryption methods." />
          </ListItem>
          <ListItem>
            <ListItemIcon><CancelIcon color="error" /></ListItemIcon>
            <ListItemText primary="Vulnerable to brute force attacks." />
          </ListItem>
          <ListItem>
            <ListItemIcon><CancelIcon color="error" /></ListItemIcon>
            <ListItemText primary="Not suitable for long text encryption as it is easy to crack." />
          </ListItem>
          <ListItem>
            <ListItemIcon><CancelIcon color="error" /></ListItemIcon>
            <ListItemText primary="Does not provide confidentiality, integrity, and authenticity." />
          </ListItem>
        </List>
        
        <Typography variant="h5" gutterBottom>
          Features of Caesar Cipher
        </Typography>
        <List>
          <ListItem>
            <ListItemIcon><RotateRightIcon color="primary" /></ListItemIcon>
            <ListItemText primary="Substitution cipher: Each letter is replaced by another letter." />
          </ListItem>
          <ListItem>
            <ListItemIcon><RotateRightIcon color="primary" /></ListItemIcon>
            <ListItemText primary="Fixed key: The number of positions by which the letters are shifted." />
          </ListItem>
          <ListItem>
            <ListItemIcon><RotateRightIcon color="primary" /></ListItemIcon>
            <ListItemText primary="Symmetric encryption: The same key is used for both encryption and decryption." />
          </ListItem>
          <ListItem>
            <ListItemIcon><RotateRightIcon color="primary" /></ListItemIcon>
            <ListItemText primary="Limited keyspace: Only 26 possible keys." />
          </ListItem>
          <ListItem>
            <ListItemIcon><RotateRightIcon color="primary" /></ListItemIcon>
            <ListItemText primary="Easy to implement: Requires simple arithmetic operations." />
          </ListItem>
        </List>
        
        <Typography variant="h5" gutterBottom>
          Rules for the Caesar Cipher
        </Typography>
        <List>
          <ListItem>
            <ListItemIcon><InfoIcon /></ListItemIcon>
            <ListItemText primary="Choose a number between 1 and 25. This will be your “shift” value." />
          </ListItem>
          <ListItem>
            <ListItemIcon><InfoIcon /></ListItemIcon>
            <ListItemText primary="Write down the letters of the alphabet in order, from A to Z." />
          </ListItem>
          <ListItem>
            <ListItemIcon><InfoIcon /></ListItemIcon>
            <ListItemText primary="Shift each letter of the alphabet by the “shift” value." />
          </ListItem>
          <ListItem>
            <ListItemIcon><InfoIcon /></ListItemIcon>
            <ListItemText primary="Encrypt your message by replacing each letter with the corresponding shifted letter." />
          </ListItem>
          <ListItem>
            <ListItemIcon><InfoIcon /></ListItemIcon>
            <ListItemText primary="To decrypt the message, simply reverse the process by shifting each letter back by the same amount." />
          </ListItem>
        </List>
        
        <Typography variant="h5" gutterBottom>
          Example
        </Typography>
        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <Typography variant="body1" paragraph>
            Text : ABCDEFGHIJKLMNOPQRSTUVWXYZ
            Shift: 23
            Cipher: XYZABCDEFGHIJKLMNOPQRSTUVW
          </Typography>
          <Typography variant="body1" paragraph>
            Text : ATTACKATONCE
            Shift: 4
            Cipher: EXXEGOEXSRGI
          </Typography>
        </Paper>
        
        <Typography variant="h5" gutterBottom>
          Conclusion
        </Typography>
        <Typography variant="body1" paragraph>
          The Caesar Cipher, with its straightforward approach of shifting letters, serves as an excellent introduction to the world of cryptography. While it is easy to understand and implement, its simplicity also makes it vulnerable to basic attacks. Despite these limitations, the Caesar Cipher’s historical role is significant, representing the early efforts to secure communication and paving the way for more advanced encryption methods used today.
        </Typography>
        
        <Typography variant="h5" gutterBottom>
          Frequently Asked Questions (FAQs)
        </Typography>
        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <Typography variant="body1" paragraph>
            <strong>What is the Caesar cipher in cryptography?</strong>
            <br />
            The Caesar Cipher, used by Julius Caesar around 58 BC, is a method that scrambles a message by shifting its letters. For example, shifting ‘A’ by three positions makes it ‘D’. To read the message, the receiver reverses this shift.
          </Typography>
          <Typography variant="body1" paragraph>
            <strong>What is an example of decryption in Caesar cipher?</strong>
            <br />
            An example of decryption in the Caesar cipher is decrypting the message KHOOR. If the original shift used to encrypt the message was 3, you would reverse this by shifting each letter back by 3 places in the alphabet. Thus, K shifts to H, H to E, O to L, and R to O, revealing the decrypted message: HELLO.
          </Typography>
          <Typography variant="body1" paragraph>
            <strong>What are keys in Caesar cipher?</strong>
            <br />
            In the Caesar cipher, the key is a letter that shows how many places to shift each letter in the message. For example, a key D means “shift 3 places,” and a key M means “shift 12 places.” A key A means “do not shift,” and a key Z means either “shift 25 places” or “shift one place backwards.”
          </Typography>
        </Paper>
      </Section>
    </Container>
  );
};

export default Explanation;
