// src/components/Home.js
import React from 'react';
import { Container, Typography, Button, Box, Grid } from '@mui/material';
import { Link } from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import cipherImage from '../assets/download.png';

const Home = () => (
  <Container maxWidth="md">
    <Box sx={{ textAlign: 'center', py: 5 }}>
      <HomeIcon fontSize="large" color="primary" sx={{ mb: 2 }} />
      <Typography variant="h2" component="h1" gutterBottom sx={{ fontWeight: 'bold' }}>
        Welcome to the CyberArk Caesar Cipher Tool
      </Typography>
      <Typography variant="h5" component="p" gutterBottom sx={{ mb: 4 }}>
        Learn about the Caesar Cipher, try it yourself, and explore its history and applications.
      </Typography>
    </Box>
    <Box sx={{ mb: 5 }}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
          <Typography variant="h4" component="h2" gutterBottom>
            What is Caesar Cipher?
          </Typography>
          <Typography variant="body1" component="p" paragraph>
            The Caesar Cipher, also known as the shift cipher, is one of the simplest and most widely known encryption techniques. It is a type of substitution cipher in which each letter in the plaintext is 'shifted' a certain number of places down or up the alphabet. The method is named after Julius Caesar, who used it in his private correspondence.
          </Typography>
          <Typography variant="body1" component="p" paragraph>
            The Caesar Cipher involves replacing each letter of the alphabet with the letter standing three places further down the alphabet. For example, 'A' becomes 'D', 'B' becomes 'E', and so on.
          </Typography>
          <Typography variant="body1" component="p" paragraph>
            If the shift takes you past the end of the alphabet, just rotate back to the beginning. For example, 'X' becomes 'A', 'Y' becomes 'B', and 'Z' becomes 'C' with a shift of 3.
          </Typography>
        </Grid>
        <Grid item xs={12} md={6}>
          <Box
            component="img"
            src={cipherImage}
            alt="Caesar Cipher Example"
            sx={{ width: '100%', maxWidth: 400, mb: 4, mx: 'auto' }}
          />
          <Button 
            variant="contained" 
            color="primary" 
            component={Link} 
            to="/tool"
            sx={{ px: 4, py: 1.5, fontSize: '1rem' }}
          >
            Try the Cipher Tool
          </Button>
        </Grid>
      </Grid>
    </Box>
    <Box sx={{ textAlign: 'center', py: 5 }}>
      <Typography variant="h4" component="h2" gutterBottom>
        Example
      </Typography>
      <Typography variant="body1" component="p" paragraph>
        - Plaintext: HELLO
        - Shift: 3
        - Ciphertext: KHOOR
      </Typography>
    </Box>
  </Container>
);

export default Home;
