// src/components/Navigation.js
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Button, IconButton, Drawer, List, ListItem, ListItemText } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import SchoolIcon from '@mui/icons-material/School';
import styled from '@emotion/styled';

const Nav = styled.nav`
  flex-grow: 1;
`;

const Navigation = () => {
  const [drawerOpen, setDrawerOpen] = useState(false);

  const toggleDrawer = (open) => () => {
    setDrawerOpen(open);
  };

  const drawer = (
    <div onClick={toggleDrawer(false)}>
      <List>
        <ListItem button component={Link} to="/">
          <ListItemText primary="Home" />
        </ListItem>
        <ListItem button component={Link} to="/tool">
          <ListItemText primary="Tool" />
        </ListItem>
        <ListItem button component={Link} to="/explanation">
          <ListItemText primary="Explanation" />
        </ListItem>
        <ListItem button component={Link} to="/applications">
          <ListItemText primary="Applications" />
        </ListItem>
        <ListItem button component={Link} to="/resources">
          <ListItemText primary="Resources" />
        </ListItem>
      </List>
    </div>
  );

  return (
    <Nav>
      <AppBar position="static">
        <Toolbar>
          <IconButton edge="start" color="inherit" aria-label="menu" onClick={toggleDrawer(true)} sx={{ display: { xs: 'block', sm: 'none' } }}>
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" sx={{ flexGrow: 1, display: 'flex', alignItems: 'center' }}>
            <SchoolIcon fontSize="large" sx={{ mr: 1 }} /> CyberSecurity Caesar Cipher Tool
          </Typography>
          <div sx={{ display: { xs: 'none', sm: 'block' } }}>
            <Button color="inherit" component={Link} to="/">Home</Button>
            <Button color="inherit" component={Link} to="/tool">Tool</Button>
            <Button color="inherit" component={Link} to="/explanation">Explanation</Button>
            <Button color="inherit" component={Link} to="/applications">Applications</Button>
            <Button color="inherit" component={Link} to="/resources">Resources</Button>
          </div>
        </Toolbar>
      </AppBar>
      <Drawer anchor="left" open={drawerOpen} onClose={toggleDrawer(false)}>
        {drawer}
      </Drawer>
    </Nav>
  );
};

export default Navigation;
